<template>
	<div class="calculator">
		<div>
			<input v-model="valueCal" placeholder="계산기" readonly />
			<button class="text-button" @click="submitCal">계산</button>
		</div>
		<br />
		<hr />
		<div class="button-row">
			<button @click="addValues(1)">1</button>
			<button @click="addValues(2)">2</button>
			<button @click="addValues(3)">3</button>
			<button @click="addValues('+')">+</button>
		</div>
		<br />
		<div class="button-row">
			<button @click="addValues(4)">4</button>
			<button @click="addValues(5)">5</button>
			<button @click="addValues(6)">6</button>
			<button @click="addValues('-')">-</button>
		</div>
		<br />
		<div class="button-row">
			<button @click="addValues(7)">7</button>
			<button @click="addValues(8)">8</button>
			<button @click="addValues(9)">9</button>
			<button @click="addValues('*')">*</button>
		</div>
		<br />
		<div class="button-row">
			<button @click="addValues(0)" class="long">0</button>
			<button @click="addValues('/')">/</button>
		</div>
		<br />
		<h3>결과 : {{ result }}</h3>
		<br />
		<button @click="resetting">리셋</button>
	</div>
</template>

<script>
export default {
	data() {
		return {
			valueCal: "",
			result: null,
		};
	},
	methods: {
		addValues(value) {
			this.valueCal += value;
		},
		submitCal() {
			try {
				this.result = eval(this.valueCal);
				this.valueCal = "";
			} catch (error) {
				this.result = "잘못된 입력";
			}
		},
		resetting() {
			this.valueCal = "";
		},
	},
};
</script>

<style scoped>
.calculator {
	width: 400px;
	margin: 0 auto;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 20px;
	border: 2px solid #ccc;
	border-radius: 10px;
	background-color: #f9f9f9;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

hr {
	width: 100%;
	border: 3px solid lightgray;
	border-radius: 30px;
}

.button-row {
	display: flex;
	justify-content: space-between;
	margin-bottom: 10px;
}

button {
	flex: 1;
	padding: 10px;
	font-size: 18px;
	margin: 5px;
}
.text-button {
	margin-left: 20px;
	width: 90px;
	height: 45px;
}
.long {
	flex: 4;
}
</style>
